<div class="address {currentTab !== "" ? "tabOpen" : ""}" use:outsideClick={closeTab}>
    <h3 class="header">
        <span class="mdi mdi-{address.icon}"></span>
        {address.name}
    </h3>
    <InfoRow>
        <span slot="left"><span class="mdi mdi-road-variant"></span> {physicalAddress}</span>
    </InfoRow>
    <div class="buttons">
        <SoftButton icon="pencil" on:click={() => openTab("changeAddress")}>Pas aan</SoftButton>
        <SoftButton icon="format-list-numbered">Regels</SoftButton>
    </div>
    {#if currentTab === "changeAddress"}
        <div class="expandable">
            <AddressEditor bind:address={address} />
        </div>
    {/if}
</div>

<script>
import outsideClick from "svelte-outside-click";

// Components
import AddressEditor from "@/components/AddressEditor.svelte";
import InfoRow from "@/components/InfoRow.svelte";
import SoftButton from "@/components/SoftButton.svelte";
import ButtonContainer from "@/components/ButtonContainer.svelte";

// Props
export var address;

// Data
var currentTab = "";

// Computed
$: physicalAddress = address.address ? address.address : "(geen address ingesteld)";

// Functions
function openTab(tab) {
    currentTab = tab;
}

function closeTab() {
    currentTab = "";
}
</script>

<style lang="scss">.address {
  width: calc(95% - 32px - 16px);
  padding: 8px;
  margin: 16px auto;
  border-radius: 10px; }
  .address.tabOpen {
    border: 2px solid #000000; }

.header {
  margin: 0;
  margin-bottom: 2px;
  text-align: left; }

.buttons {
  width: 100%;
  margin: 0px 0;
  display: inline-block; }
  .buttons > :global(button) {
    width: 50%;
    float: left; }

.expandable {
  padding: 8px 0; }

/*# sourceMappingURL=Address.svelte.css.map */</style>