{#if isCurrentStep && firstStep}
    <svelte:component this={LayoutCenter}>
        <div class="routeStep firstStep {travelMode} {stepType}" style="border-color: {borderColor};"></div>
    </svelte:component>
{/if}
<svelte:component this={currentLayout}>
    <div class="routeStep {travelMode} {isCurrentStep && "current"} {stepType}" style="border-color: {borderColor};">
        <svelte:component this={stepInfoComponent} {step} />
    </div>
</svelte:component>
{#if isCurrentStep && lastStep}
    <svelte:component this={LayoutCenter}>
        <div class="routeStep firstStep {travelMode} {stepType}" style="border-color: {borderColor};"></div>
    </svelte:component>
{/if}

<script>
import { getStepColor, getStepTextColor, getStepType, getStepInfoComponent } from "@/js/stepUtils.js";
import { onMount } from "svelte";
import { locationStore } from "@/js/locationUtils.js";
import { getCurrentStep, routeStore, getSteps } from "@/js/routeUtils.js";
import { toTimeString } from "@/js/timeUtils.js";

// Components
import LayoutCenter from "@/components/Layout/LayoutCenter.svelte";
import LayoutExpanded from "@/components/Layout/LayoutExpanded.svelte";

// Props
export var step;

// Computed
$: travelMode = step.travel_mode.toLowerCase();
$: transitDetails = travelMode === "transit" ? step.transit_details : {};
$: stepType = getStepType(step);
$: borderColor = getStepColor(stepType, transitDetails);
$: textColor = getStepTextColor(stepType, transitDetails);
$: stepInfoComponent = getStepInfoComponent(stepType);
$: isCurrentStep = getIsCurrentStep($locationStore);
$: firstStep = step.index === 0;
$: lastStep = step.index === getSteps().length - 1;
$: currentLayout = isCurrentStep ? LayoutExpanded : LayoutCenter;

// Functions
function getIsCurrentStep() {
    var currentStep = getCurrentStep();
    return currentStep.index === step.index;
}
</script>

<style lang="scss">.routeStep {
  min-height: 16px;
  padding: 16px 16px;
  margin: 0 auto;
  border: 0;
  border-left: 2px var(--stepWalkingStyle) var(--stepWalkingColor); }
  .routeStep.firstStep {
    min-height: 10px;
    padding: 0px 16px; }
  .routeStep.walking {
    border-style: var(--stepWalkingStyle); }
  .routeStep.transit.bus {
    border-style: var(--stepBusStyle); }
  .routeStep.transit.train {
    border-style: var(--stepTrainStyle); }
  .routeStep.current {
    padding: 16px;
    border-radius: 10px;
    border: 0;
    box-shadow: var(--defaultBoxShadow); }

/*# sourceMappingURL=Step.svelte.css.map */</style>