<section>
    {#if !selectOriginOpen}
        <div in:fly="{flyUp}">
            <AddressRouteHeader address={selectedOrigin} on:click={() => selectOriginOpen = true} />
        </div>
    {/if}
    <SmallDropDown text="Selecteer startlocatie" bind:open={selectOriginOpen}>
        {#each originAddresses as address}
            <AddressRouteOption {address} on:click="{() => selectOriginAddress(address.id)}" />
        {/each}
    </SmallDropDown>

    <LabelDivider text="Naar" />

    {#if !selectDestinationOpen}
        <div in:fly="{flyUp}">
            <AddressRouteHeader address={selectedDestination} on:click={() => selectDestinationOpen = true} />
        </div>
    {/if}
    <SmallDropDown text="Selecteer bestemming" bind:open={selectDestinationOpen}>
        {#each destinationAddresses as address}
            <AddressRouteOption {address} on:click="{() => selectDestinationAddress(address.id)}" />
        {/each}
    </SmallDropDown>
    
    <Button icon="magnify" on:click={searchTrips}>Zoek</Button>
</section>

<script>
// Imports
import { addressStore, getCompleteAddressMap, getOriginAddresses, getDestinationAddresses } from "@/js/addressUtils.js";
import { fromIdStore, toIdStore, doSearch } from "@/js/tripUtils.js";
import { createEventDispatcher } from "svelte";
import { fly } from "svelte/transition";
import { flyUp } from "@/js/defaultTransitions.js";

const dispatch = createEventDispatcher();

// Components
import LayoutExpanded from "@/components/Layout/LayoutExpanded.svelte";
import AddressRouteHeader from "@/components/AddressRouteHeader.svelte";
import AddressRouteOption from "@/components/AddressRouteOption.svelte";
import LabelDivider from "@/components/LabelDivider.svelte";
import SmallDropDown from "@/components/SmallDropDown.svelte";
import Button from "@/components/Button.svelte";

// Data
var selectOriginOpen = false;
var selectDestinationOpen = false;
var selectedOriginAddressId = "currentLocation";
var selectedDestinationAddressId = "auto";

// Computed
$: addressMap = getCompleteAddressMap($addressStore);
$: originAddresses = getOriginAddresses($addressStore);
$: destinationAddresses = getDestinationAddresses($addressStore);
$: selectedOrigin = addressMap[selectedOriginAddressId];
$: selectedDestination = addressMap[selectedDestinationAddressId];

// Functions
function selectOriginAddress(id) {
    selectedOriginAddressId = id;
    selectOriginOpen = false;
}

function selectDestinationAddress(id) {
    selectedDestinationAddressId = id;
    selectDestinationOpen = false;
}

function searchTrips() {
    doSearch();
    dispatch("search");
}
</script>

<style lang="scss">section {
  margin: 16px auto; }

:global(.addressRouteHeader) {
  cursor: pointer; }

.optionLayout {
  width: calc(100% - 16px);
  margin: 0 auto; }

label {
  margin-bottom: 4px; }

select {
  width: 100%;
  margin-bottom: 16px;
  padding: .4em;
  box-sizing: border-box;
  outline: 0;
  border: 1px solid var(--textColor);
  border-radius: var(--defaultBorderRadius);
  background-color: transparent;
  font-weight: 700; }

/*# sourceMappingURL=TripComposer.svelte.css.map */</style>