<section class="trips">
    <LayoutCenter>
        {#if !showResults}
            <TripComposer on:search={onSearch} />
        {/if}
    </LayoutCenter>
    {#if showResults}
        <LayoutCenter>
            <Button on:click={redoSearch}>Zoek opnieuw</Button>
        </LayoutCenter>
        {#each $availableTripsStore as trip}
            <Trip {trip} />
        {:else}
            <LayoutCenter>
                <EmptyState>Geen routes gevonden</EmptyState>
            </LayoutCenter>
        {/each}
    {/if}
</section>

<script>
// Stores
import { availableTripsStore, startAutoPlanner, getTripFromAddress, getTripToAddress } from "@/js/tripUtils.js";
import { addressStore } from "@/js/addressUtils.js";

// Components
import Trip from "@/components/Trip.svelte";
import LayoutCenter from "@/components/Layout/LayoutCenter.svelte";
import EmptyState from "@/components/EmptyState.svelte";
import TripComposer from "@/components/TripComposer.svelte";
import Popup from "@/components/Popup.svelte";
import Button from "@/components/Button.svelte";

// Data
var showResults = false;

// Computed
$: fromAddress = getTripFromAddress($availableTripsStore);
$: toAddress = getTripFromAddress($availableTripsStore);

// Function
function onSearch() {
    showResults = true;
}

function redoSearch() {
    showResults = false;
}

// On create
startAutoPlanner();
</script>

