<button class="{isCurrentPage ? "current" : ""}" on:click={onClick}>
    <span class="mdi mdi-{icon}"></span>
    <slot />
</button>

<script>
import { location, push as pushRoute } from "svelte-spa-router";
import { createEventDispatcher } from "svelte";
const dispatch = createEventDispatcher();

// Props
export var icon;
export var path = "/";

// Computed
$: isCurrentPage = path === $location;

// Functions
function onClick() {
    dispatch("click");
    pushRoute(path);
}
</script>

<style lang="scss">button {
  height: 48px;
  width: 100%;
  margin: 0;
  background-color: transparent;
  border: 0;
  font-size: 20px; }
  button span {
    margin-right: 10px; }
  button.current {
    color: var(--accentColor); }

