<div class="routeGuide">
    {#if started}
        <div class="tripStartInfo">
            <p><span class="mdi mdi-flag"></span> Vertrek om <b>{departureTime}</b></p>
        </div>
        {#each steps as step}
            <Step {step} />
        {/each}
        <div class="tripStartInfo">
            <p><span class="mdi mdi-flag-checkered"></span> Aankomst om <b>{arrivalTime}</b></p>
        </div>
    {:else}
        <LayoutCenter>
            <EmptyState>Geen route gestart</EmptyState>
        </LayoutCenter>
    {/if}
</div>
<ButtonContainer buttons={1}>
    {#if started}
        <Button on:click={endRoute} icon="stop-circle-outline">Stop route</Button>
    {/if}
</ButtonContainer>

<script>
import { startLocation } from "@/js/locationUtils.js";
import { toTimeString } from "@/js/timeUtils.js";

// Components
import Step from "@/components/Step.svelte";
import ButtonContainer from "@/components/ButtonContainer.svelte";
import Button from "@/components/Button.svelte";
import EmptyState from "@/components/EmptyState.svelte";
import LayoutCenter from "@/components/Layout/LayoutCenter.svelte";

// Stores
import { routeStore, getSteps, endRoute } from "@/js/routeUtils.js";

// Computed
$: steps = getSteps($routeStore);
$: started = steps.length > 0;
$: leg = started ? $routeStore.legs[0] : {};
$: departureTime = started ? toTimeString(leg.departure_time.value) : "";
$: arrivalTime = started ? toTimeString(leg.arrival_time.value) : "";

// On create
startLocation();
</script>

<style lang="scss">.tripStartInfo {
  width: calc(95% - 48px);
  margin: 8px auto; }
  .tripStartInfo p {
    margin: 0; }
    .tripStartInfo p span {
      font-size: 18px;
      margin-right: 8px; }

.routeGuide {
  width: 100%;
  padding: 16px 0; }
  .routeGuide > :global(.emptyState) {
    width: calc(95% - 32px - 30px); }

/*# sourceMappingURL=RouteGuide.svelte.css.map */</style>