<header class="{menuOpen ? "open" : ""}">
    <img src="./img/header-image.png" alt="">
    <div class="leftButtons">
        <HeaderButton icon={"menu"} on:click={toggleMenuOpen} />
    </div>
    <div class="rightButtons">
        <HeaderButton icon={"magnify"} />
    </div>
    <div class="menuContent">
        <MenuButton icon="compass-outline" on:click={closeMenu} path="/">Navigatie</MenuButton>
        <MenuButton icon="bookmark-multiple-outline" on:click={closeMenu} path="/addressbook">Address boek</MenuButton>
    </div>
</header>
<div class="menuScim {menuOpen ? "open" : ""}" on:click|self={closeMenu}></div>

<script>
// Components
import HeaderButton from "@/components/HeaderButton.svelte";
import MenuButton from "@/components/MenuButton.svelte";

// Data
var menuOpen = false;

// Functions
function toggleMenuOpen() {
    menuOpen = !menuOpen;
}

function closeMenu() {
    menuOpen = false;
}
</script>

<style lang="scss">header {
  min-height: 48px;
  width: 100%;
  position: relative;
  z-index: 6;
  background-color: var(--backgroundColor); }
  header img {
    height: 38.4px;
    position: relative;
    top: 14px;
    left: 50%;
    transform: translateX(-50%); }
  header .rightButtons {
    position: absolute;
    top: 8px;
    right: 8px; }
  header .leftButtons {
    position: absolute;
    top: 8px;
    left: 8px; }
  header.open .menuContent {
    max-height: 900px; }
  header .menuContent {
    max-height: 0;
    margin-top: 16px;
    padding-bottom: 10px;
    overflow: hidden;
    transition: 250ms cubic-bezier(0.26, 0, 0, 0.99) max-height; }

.menuScim {
  height: 100%;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: -1;
  background-color: var(--scimColor);
  opacity: 0;
  transition: 200ms cubic-bezier(0.4, 0, 0.2, 1) opacity; }
  .menuScim.open {
    z-index: 2;
    opacity: 1; }

/*# sourceMappingURL=Header.svelte.css.map */</style>