<button on:click={clickEvent}>
    <span class="mdi mdi-{icon}"></span>
    <slot />
</button>

<script>
import { createEventDispatcher } from "svelte";
const dispatch = createEventDispatcher();

// Props
export var icon = "";

// Function
function clickEvent() {
    dispatch("click");
}
</script>

