<button on:click={clickEvent}>
    {#if iconPresent}
        <span class="mdi mdi-{icon}"></span>
    {/if}
    <slot />
</button>

<script>
import { createEventDispatcher } from "svelte";
const dispatch = createEventDispatcher();

// Props
export var icon = null;

// Computed
$: iconPresent = icon !== null;

// Function
function clickEvent() {
    dispatch("click");
}
</script>

