<p on:click={toggleDropdown}><span class="mdi mdi-chevron-down"></span> {text}</p>
{#if open}
    <div class="dropDownContent" in:fly={flyDown}>
        <slot />
    </div>
{/if}

<script>
// Imports
import { fly } from "svelte/transition";
import { flyDown } from "@/js/defaultTransitions.js";

// Props
export var text = "Meer Info";
export var open = false;

// Functions
function toggleDropdown() {
    open = !open;
}
</script>

<style lang="scss">p {
  width: 100%;
  margin-bottom: 0;
  cursor: pointer;
  text-align: center; }

.dropDownContent {
  padding-top: 16px; }

/*# sourceMappingURL=DropDown.svelte.css.map */</style>