<InfoRow>
    <span slot="left">{startTimeString}</span>
    <span slot="center">
        <HeadSign icon="train" {backgroundColor} {textColor}>{line.short_name}</HeadSign>
    </span>
    <span slot="right">{endTimeString}</span>
</InfoRow>
{#if cancelled}
    <p class="cancelled">
        <span class="mdi mdi-alert"></span>
        Deze trein is geannuleerd
    </p>
{/if}
<InfoRow>
    <span slot="left"><span class="mdi mdi-arrow-expand-right"></span> {transitDetails.departure_stop.name}</span>
    <span slot="right">spoor {originPlatform}</span>
</InfoRow>
<InfoRow>
    <span slot="left"><span class="mdi mdi-arrow-collapse-right"></span> {transitDetails.arrival_stop.name}</span>
    <span slot="right">spoor {destinationPlatform}</span>
</InfoRow>
<InfoRow>
    <span slot="left">
        <span class="mdi mdi-rss {liveDataAvailable ? "liveDataActive" : ""}"></span> 
        {#if liveDataAvailable}
            {liveMessage}
        {:else}
            Geen live data beschikbaar
        {/if}
    </span>
</InfoRow>
<DropDown>
    {#if trainDataStatus === "loaded"}
        <div class="trainImages">
            {#each trainData.materieeldelen as trainPart}
                <img class="trainPartImage" src={trainPart.afbeelding} alt="Train part" />
            {/each}
        </div>
        {#if exitDirection}
            <p class="exitDirection">
                {#if exitDirection === "LEFT"}
                    <span class="mdi mdi-chevron-triple-left"></span> Links uitstappen
                {:else}
                    Rechts uitstappen <span class="mdi mdi-chevron-triple-right"></span>
                {/if}
            </p>
        {/if}
        <InfoRow>
            <span slot="left"><span class="mdi mdi-routes"></span> Richting</span>
            <span slot="right">{transitDetails.headsign}</span>
        </InfoRow>
        <InfoRow>
            <span slot="left"><span class="mdi mdi-ruler"></span> Lengte</span>
            <span slot="right"><b>{trainData.lengte}</b> delen</span>
        </InfoRow>
        {#each trainData.materieeldelen as trainPart}
            <InfoRow>
                <span slot="left"><span class="mdi mdi-barcode"></span> Treinstel {trainPart.materieelnummer}</span>
                <span slot="right">
                    {#each trainPart.faciliteiten as facility}
                        <span class="facilityIcon mdi mdi-{getFacilityIcon(facility)}"></span>
                    {/each}
                </span>
            </InfoRow>
        {/each}
    {:else if trainDataStatus === "error"}
        <p>Error</p>
    {/if}
</DropDown>

<script>
import { onMount, onDestroy } from "svelte";
import { serverRequest } from "@/js/dataUtils.js";
import { getStepColor, getStepTextColor, createStepStore } from "@/js/stepUtils.js";
import { standardizeTimestamp, toTimeString } from "@/js/timeUtils.js";
import { getFacilityIcon, getTrainTrip, refreshTrainTrip, getStationCodeByLocation, getFullDetailsByCode, LiveTrainLocation } from "@/js/trainUtils.js";

// Components
import InfoRow from "@/components/InfoRow.svelte";
import HeadSign from "@/components/HeadSign.svelte";
import DropDown from "@/components/DropDown.svelte";

// Props
export var step;

// Data
const refreshRate = 60 * 1000; // 60 seconds

var stepStore = createStepStore(step.id, {});
$stepStore.maxAge = 60 * 60 * 1000; // 60 minutes;
$stepStore.timestamp = Date.now();

var trainDataStatus = "";
var trainData;
var trainTrip;

var liveData;
var liveMessage;

var polyline = [];

// Computed
$: transitDetails = step.transit_details;
$: tripNumber = transitDetails.trip_short_name;
$: line = transitDetails.line;
$: backgroundColor = getStepColor("train", transitDetails);
$: textColor = getStepTextColor("train", transitDetails);
$: startTimestamp = standardizeTimestamp(transitDetails.departure_time.value);
$: startTimeString = toTimeString(startTimestamp);
$: endTimestamp = standardizeTimestamp(transitDetails.arrival_time.value);
$: endTimeString = toTimeString(endTimestamp);
$: tripRefreshCode = $stepStore.tripRefreshCode;
$: tripLoaded = trainTrip !== undefined && tripRefreshCode !== undefined;
$: trainTripLeg = tripLoaded ? trainTrip.legs[0] : null;
$: originPlatform = tripLoaded ? trainTripLeg.origin.plannedTrack : "?";
$: destinationPlatform = tripLoaded ? trainTripLeg.destination.plannedTrack : "?";
$: exitDirection = tripLoaded ? trainTripLeg.destination.exitSide : false;
$: cancelled = tripLoaded ? trainTripLeg.cancelled : false;
$: stops = tripLoaded ? trainTripLeg.stops : [];
$: liveDataAvailable = liveMessage;

// Functions
async function refreshTrip() {
    if ($stepStore.tripRefreshCode) {
        trainTrip = await refreshTrainTrip($stepStore.tripRefreshCode);
        var stopCodes = getStopCodes(trainTrip.legs[0].stops);
        var { coords } = await serverRequest(`/trainRouteCoords?stations=${stopCodes.join(",")}`, 60 * 60 * 1000); // Cached for an hour
        polyline = coords;
    }
}

function getStopCodes(stops) {
    var codes = stops.map(stop => getStationCodeByLocation(stop.lat, stop.lng).stationCode);
    return codes;
}

// TODO move this into locationUtils.js
function findClosestPoint(points, lat, long) {
    points = points.map(point => {
        var differenceLat = Math.abs(point.lat - lat);
        var differenceLong = Math.abs(point.long - long);

        var directDifference = Math.sqrt(differenceLat ** 2 + differenceLong ** 2);

        return {
            ...point,
            directDifference,
        }
    })
    .sort((a, b) => a.directDifference - b.directDifference);

    return points[0];
}

// On create
const refreshTimer = setInterval(refreshTrip, refreshRate);
refreshTrip();

// On mount
onMount(async () => {
    try {
        liveData = new LiveTrainLocation(tripNumber);
        trainData = await serverRequest("/virtualTrain/" + tripNumber);

        liveData.subscribe(data => {
            if (!data) {
                liveMessage = null;
                return;
            }

            var closestPoint = findClosestPoint(polyline, data.lat, data.lng);

            if (closestPoint) {
                var closestStation = getFullDetailsByCode(closestPoint.code);

                if (data.snelheid === 0) {
                    liveMessage = `Trein aangekomen op ${closestStation.namen.middel}`;
                } else {
                    liveMessage = `Trein rijd naar ${closestStation.namen.middel}`;
                }
            } else {
                liveMessage = null;
            }
        });
        
        liveData.start();
        trainDataStatus = "loaded";
    } catch(error) {
        trainDataStatus = "error";
        throw error;
    }

    if ($stepStore.tripRefreshCode === undefined) {
        var tripData = await getTrainTrip(step);

        if (tripData.length > 0) {
            trainTrip = tripData[0];
            $stepStore.tripRefreshCode = trainTrip.uid;
            $stepStore = $stepStore;
        }
    }
});

// On destroy
onDestroy(() => {
    clearInterval(refreshTimer);
    liveData.destroy();
});
</script>

<style lang="scss">.trainImages {
  width: 100%;
  margin-bottom: 16px; }
  .trainImages .trainPartImage {
    max-height: 32px;
    width: 100%; }

.facilityIcon {
  margin-left: 4px; }

.exitDirection {
  text-align: center; }

.cancelled {
  color: var(--errorColor);
  text-align: center;
  font-weight: 700; }

.liveDataActive {
  color: var(--liveDataActive); }

