<p on:click={toggleDropdown}><span class="mdi mdi-chevron-{open ? "up" : "down"}"></span> {text}</p>
{#if open}
    <div class="dropDownContent" in:fly={flyDown}>
        <slot />
    </div>
{/if}

<script>
// Imports
import { fly } from "svelte/transition";
import { flyDown } from "@/js/defaultTransitions.js";

// Props
export var text = "Meer Info";
export var open = false;

// Functions
function toggleDropdown() {
    open = !open;
}
</script>

<style lang="scss">p {
  margin-top: 4px;
  margin-bottom: 20px;
  text-align: center;
  font-size: 14px;
  cursor: pointer; }

/*# sourceMappingURL=SmallDropDown.svelte.css.map */</style>