<input type="text" placeholder="Zoek locatie" bind:value={query} on:keydown={onKeyPress} />
{#each results as result}
    <div class="result" on:click={() => chooseResult(result)}>
        <p>{result.properties.search}</p>
    </div>
{/each}

<script>
import { createEventDispatcher } from "svelte";
const dispatch = createEventDispatcher();

// Props
export var addressString;
export var coordinates = {
    lat: 0,
    long: 0,
};

// Data
var query = "";
var results = [];
var timer;

// Functions
function onKeyPress() {
    const timeoutValue = 1000;
    if (timer) clearTimeout(timer);

    timer = setTimeout(async () => {
        var response = await fetch(`https://1313.nl/geocoder/${query}*`)
            .then(r => r.json());

        results = response.features;
    }, timeoutValue);
}

function chooseResult(result) {
    var [long, lat] = result.geometry.coordinates;

    query = result.properties.search;
    addressString = query;

    coordinates.lat = lat;
    coordinates.long = long;

    coordinates = coordinates;
    dispatch("choise");
}
</script>

<style lang="scss">input {
  width: 100%;
  margin: 0;
  outline: 0;
  border: 2px solid var(--lowContrastColor);
  border-radius: 5px; }

.result {
  width: calc(90% - 16px);
  padding: 8px;
  margin: 0 auto;
  border-left: 2px dashed var(--lowContrastColor); }
  .result p {
    margin: 0; }

/*# sourceMappingURL=AddressSearchBar.svelte.css.map */</style>